@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
* 
{
  font-family: 'Rubik', sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.containerOne {
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.box {
    width: 90%;
    height: 50px;
    padding: 5px;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.first-box{
    width: 70%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
.second-box{
    width: 30%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-right: 30px;
    align-items: center;
}

.linksOne{
    text-decoration: none;
    color: black;
    font-size: 13px;
}

.linksTwo {
    text-decoration: none;
    color: #4060bf;
    font-size: 15px;
}

.line {
    width: 100%;
    background-color: rgb(202, 202, 202);
}

.first-box img {
    width: 60px;
    margin-top: 10px;
}

.firstSection-main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  background-color: whitesmoke;
}

.firstSection-main-one {
  width: 80%;
  height: 50vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.firstSection-main-two {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.firstSection-main-two .cardsOne,
.firstSection-main-two .cardsTwo {
  width: 85%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.firstSection-main-two .cardsOne .cards,
.firstSection-main-two .cardsTwo .cards {
  width: 320px;
  height: 200px;
  background-color: white;
  border-radius: 10px;
  margin: 20px;
  /* box-shadow: 2px 2px 5px 2px royalblue */
  /* box-shadow: 7px 7px 8px rgba(100, 100, 100, 0.5); */
  box-shadow: 0 0 6px -3px;
}

.words {
  padding: 10px;
  color: #4060bf;
}

